<template>
  <div class="pd-1 flex-column" v-loading="loading"> 
    <div class="flex-column">
      <div class="ml-auto pure-button pure-button-primary">
        <router-link to="/workshop/cards">
        创建卡片
        </router-link>
      </div>
      <form 
      @submit.prevent=""
      class="pure-form mt-1 flex-row"
      >
        <input type="text" name="cards-search" id="cards-search"
          placeholder="search"
          v-model="keyword"
          class="ml-auto"
        >
        <select class="ml-1" v-model="view" name="cards-view" id="cards-view">
          <option value="table-view">表单</option>
          <option value="grid-view">卡片</option>
        </select>
      </form>
    </div>
    <component 
      class="mt-1 flex-1"
      v-if="!loading"
      :cards="cards"
      :is="view"
    />
    <pager
    class="mt-auto"
    :total="total"
    :active="page"
    @click="toPage"
    />
  </div>
</template>

<script>
import debounce from '@/tools/debounce';
import config from '@/config';

import TableView from './components/table.vue';
import GridView from './components/grid.vue';


export default {
  components: {
    TableView,
    GridView,
  },
  data() {
    return {
      cards: [],

      page: 1,
      total: 0,

      keyword: '',

      view: 'table-view',
      loading: true,
    }
  },
  watch: {
    keyword() {
      this.getData();
    },
  },
  mounted() {
    this.getData();
  },
  methods: {
    getData: debounce(function () {
      this.$axios.get(`${this.$url.cards}?page=${this.page}&keyword=${this.keyword}`)
        .then((resp) => {
          this.cards = resp.data.results;
          this.total = resp.data.total;
          this.loading = false;
        })
        .catch(console.log);
    }, config.debounceDelay),

    toPage(page) {
      this.page = page;
      this.getData();
    },
  }
}
</script>