<template>
  <div v-if="cards.length > 0" class="cardview">
    <card-group-2
    v-for="(card, index) in cards"
    :key="card.title + index"
    :data="card"
    @click="toCard(card)"
    >
    </card-group-2>
  </div>
  <no-data v-else></no-data>
</template>

<script>
export default {
  props: {
    cards: {
      type: Array,
      required: true,
    }
  },
  methods: {
    toCard(card) {
      this.$router.push('/cards/' + card.title);
    }
  }
}
</script>

<style lang="scss" scoped>
.cardview {
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  grid-gap: 0.8em;
  grid-auto-rows: minmax(100px, auto);
}
</style>
