<template>
  <table v-if="cards.length > 0" class="mt-1 cards-table pure-table pure-table-horizontal w-100 table-fixed">
    <thead >
      <tr>
        <th>标题</th>
        <th>作者</th>
        <th>分類</th>
        <th>創建時間</th>
      </tr>
    </thead>
    <tbody>
      <tr v-for="card in cards" :key="card.title">
        <td :title="card.title">
          <router-link :to="'/cards/' + card.title">
            {{ card.title }}
          </router-link>
        </td>
        <td :title="card.author" >{{ card.author }}</td>
        <td :title="card.category.toString()" >{{ card.category.toString() }}</td>
        <td :title="card.createdAt | formatDateTime" >{{ card.createdAt | formatDateTime }}</td>
      </tr>
    </tbody>
  </table>
  <no-data v-else></no-data>
</template>
<script>
export default {
  props: {
    cards: {
      type: Array,
      required: true,
    }
  }
}
</script>

<style lang="scss" scoped>
.cards-table {
  td {
    word-break: break-all;
  }
}
</style>
